@charset "UTF-8";
/** Media Queries **/
img,
picture {
  width: 100%;
  display: block;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

/** UI **/
* {
  margin: 0;
}

header {
  background-color: #FFC107;
  /*background-color: rgb(230, 227, 221);*/
  width: 100%;
  height: 90px;
  display: flex;
  padding: 5px;
}

.container-logo {
  width: 40%;
  display: flex;
  align-items: center;
}

.logo {
  text-decoration: none;
  color: black;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px;
}

.logo .nombre {
  font-family: "Montserrat", system-ui;
  font-optical-sizing: auto;
  font-size: 2rem;
  font-style: normal;
  padding-bottom: 5px;
}

.logo .slogan {
  font-family: "Montserrat", system-ui;
  font-optical-sizing: auto;
  font-size: 1rem;
  font-style: normal;
}

.container-menu {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-around;
  width: 100%;
  align-items: center;
}

ul {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-around;
  align-items: center;
}

ul li {
  list-style: none;
  margin: 0 10px;
  padding: 5px;
}

ul li a {
  color: black;
  text-decoration: none;
}

ul li a p:hover {
  text-decoration: white underline 2px;
  transition: ease-out 0.2s;
  color: white;
  font-size: 1rem;
  font-weight: semi-bold;
}

ul li a p {
  font-family: "Montserrat", system-ui;
  font-optical-sizing: auto;
  font-size: 0.9rem;
  font-style: normal;
}

ul li select {
  font-family: "Montserrat", system-ui;
  font-optical-sizing: auto;
  font-size: 0.9rem;
  font-style: normal;
  color: black;
  background-color: rgb(230, 227, 221);
  border: none;
  border-radius: 5px;
  width: 100%;
  height: 30px;
}

* {
  margin: 0;
}

footer {
  background-color: rgb(230, 227, 221);
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  height: 80px;
}

footer div {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 50px;
  margin: 0 10px;
  align-items: center;
}

footer div.cp {
  display: flex;
  flex-direction: column;
  float: left;
  width: 100%;
  height: 50px;
  margin: 0 10px;
  align-items: flex-start;
}

footer div.cp div {
  align-items: center;
  justify-content: flex-start;
}

footer img {
  width: 20px;
  height: 20px;
  margin-left: 10px;
}

footer div a {
  color: black;
  text-decoration: none;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 5px;
}

footer .redes {
  display: flex;
  flex-direction: row;
}

.texto-footer {
  font-family: "Montserrat", system-ui;
  font-size: 12px;
}

.direccion {
  font-family: "Montserrat", system-ui;
  font-size: 10px;
  margin-left: 10px;
}

* {
  margin: 0;
}

body {
  background-image: url(../img/fondo2.webp);
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  background-position: center;
}

@media (min-width: 768px) {
  .contenedor-posts {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
    padding-top: 2rem;
  }
}
.contenedor-posts {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin-right: 10px;
  height: 33vmax;
}

.post {
  background-color: rgb(226.5, 226.5, 226.5);
  border: 1px solid rgb(202.5, 202.5, 202.5);
  padding: 4px;
  margin-bottom: 2rem;
  width: 20%;
  margin: 0 auto;
}
.post .contenido-post {
  padding: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.post h3, .post p {
  font-family: "Montserrat", system-ui;
  font-size: 1rem;
  font-style: thin;
  color: rgb(46, 45, 42);
  padding-top: 5px;
  padding-bottom: 5px;
  text-align: center;
  width: 100%;
}
.post .contenido-post p a .ver-perfil {
  text-align: center;
}
.post a {
  background-color: rgba(155, 149, 149, 0.7176470588);
  color: #FFFFFF;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.post a:hover {
  background-color: rgba(142.6213592233, 135.8786407767, 135.8786407767, 0.7176470588);
  cursor: pointer;
}

.nombre-form {
  font-family: "Montserrat", system-ui;
  font-size: 1.6rem;
  font-style: thin;
  color: #455A64;
  text-align: center;
  padding-top: 20px;
  padding-bottom: 10px;
}

.container-form {
  width: 100%;
  display: flex;
  justify-content: center;
}

.formulario {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px;
  border: none;
  border-radius: 10px;
  background-color: rgba(245, 243, 239, 0.502);
  width: 70%;
  height: 450px;
  padding: 10px;
}

.formulario fieldset {
  height: 400px;
  width: 60%;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-left: 20px;
  flex-direction: column;
  margin-top: 20px;
  padding: 20px;
  padding-left: 50px;
  padding-right: 50px;
}

.formulario legend {
  font-family: "Montserrat", system-ui;
  font-size: 1.6rem;
  font-style: thin;
  color: rgb(27, 27, 26);
  text-align: left;
  padding-top: 5px;
  padding-bottom: 5px;
}

.formulario fieldset {
  display: flex;
  flex-direction: column;
}

.container-label {
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: space-between;
}

.container-label label {
  font-family: "Montserrat", system-ui;
  font-size: 1rem;
  font-style: thin;
  color: rgb(27, 27, 26);
  padding-top: 5px;
  padding-bottom: 5px;
  padding-right: 20px;
}

.container-label input, select, textarea {
  font-family: "Montserrat", system-ui;
  font-size: 0.7rem;
  font-style: thin;
  color: rgb(27, 27, 26);
  text-align: left;
  padding-top: 5px;
  padding-bottom: 5px;
  border: none;
  border-radius: 10px;
  width: 50%;
  height: 25px;
  display: flex;
  float: left;
}

.container-label input:focus {
  outline: none;
  border: 1px solid rgb(111, 109, 103);
}

button[type=submit] {
  background-color: rgb(111, 109, 103);
  color: white;
  font-size: 1.2rem;
  font-style: thin;
  border: none;
  border-radius: 10px;
  width: 30%;
  height: 30px;
  margin: 20px;
}

button[type=submit]:hover {
  background-color: rgb(230, 227, 221);
  color: rgb(111, 109, 103);
  border: 1px solid rgb(111, 109, 103);
}

.container-perfil {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px;
  border: none;
  border-radius: 10px;
  background-color: rgba(230, 227, 221, 0.623);
  width: 60%;
  height: 300px;
  margin-left: 280px;
  align-self: center;
  justify-self: center;
}

.container-perfil p {
  font-family: "Montserrat", system-ui;
  font-size: 1rem;
  font-style: thin;
  color: rgb(111, 109, 103);
  text-align: left;
  padding-top: 30px;
  padding-bottom: 10px;
}

* {
  box-sizing: border-box;
}

/* Slideshow container */
.slideshow-container {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 400px; /* Ajusta el alto según tus necesidades */
  margin-top: 40px; /* Espaciado desde arriba */
}

/* Ajustar las imágenes dentro del slideshow */
.mySlides {
  /* Cambia flex por block */
  width: 50%;
  height: 100%;
  position: relative;
  justify-content: center;
  align-items: center;
  text-align: center;
}

/* Ajuste de las imágenes */
.mySlides img {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain; /* Asegura que la imagen se ajuste sin deformarse */
  margin: 0 auto; /* Centra la imagen horizontalmente */
}

/* Caption text */
.text {
  color: #f2f2f2;
  font-size: 15px;
  padding: 8px 12px;
  position: absolute;
  bottom: 8px;
  width: 100%;
  text-align: center;
}

/* The dots/bullets/indicators */
.puntos {
  width: 100%;
  display: flex;
  justify-content: center;
  padding: 10px;
}

.dot {
  height: 15px;
  width: 15px;
  margin: 0 2px;
  background-color: #bbb;
  border-radius: 50%;
  display: inline-block;
  transition: background-color 0.6s ease;
  text-align: center;
}

.active {
  background-color: #717171;
}

/* Fading animation */
.fade {
  animation-name: fade;
  animation-duration: 1.5s;
}

/* Botones de navegación */
.prev, .next {
  cursor: pointer;
  position: absolute;
  top: 50%;
  width: auto;
  padding: 16px;
  margin-top: -22px;
  color: white;
  font-weight: bold;
  font-size: 18px;
  transition: 0.6s ease;
  border-radius: 0 3px 3px 0;
  user-select: none;
}

.prev {
  left: 250px;
  border-radius: 3px 0 0 3px;
}

.next {
  right: 250px;
  border-radius: 0 3px 3px 0;
}

/* Hover effects on navigation buttons */
.prev:hover, .next:hover {
  background-color: rgba(0, 0, 0, 0.8);
}

@keyframes fade {
  from {
    opacity: 0.4;
  }
  to {
    opacity: 1;
  }
}
/* On smaller screens, decrease text size */
@media only screen and (max-width: 300px) {
  .text {
    font-size: 11px;
  }
}